.name-label,
.email-label,
.description-label {
  display: block;
  margin-bottom: 8px;
}

.user-input,
.email-input,
.description-input {
  width: 100%;
  padding: 8px;
  margin-bottom: 16px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.description-input {
  height: 80px; 
}

.add-button, .cancel-button, .update-button {
  background-color: #8758ff;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.add-button, .update-button{
  margin-right: 2px;
}
.cancel-button{
  margin-left: 2px;
}

.add-button:hover, .cancel-button:hover, .update-button:hover {
  background-color: #332161;
}